"use client";
import Image from "next/image";
import React, { useEffect, useRef, useState } from "react";
import { mediaLoader } from "@/lib/media-loader";
import Link from "next/link";

interface BenefitItem {
    id: number;
    image: string;
    title: string;
}

interface ProductBenefitsProps {
    animationGif: string;
    leftSectionTitle: string;
    carousalData: BenefitItem[];
    bannerTitle: string;
    bannerSubtitle: string;
    ctaText: string;
    ctaLink: string;
}

function ProductBenefits({
    animationGif,
    leftSectionTitle,
    carousalData,
    bannerTitle,
    bannerSubtitle,
    ctaText,
    ctaLink,
}: ProductBenefitsProps) {
    const [isVisible, setIsVisible] = useState(false);
    const [currentIndex, setCurrentIndex] = useState(0);
    const sectionRef = useRef<HTMLDivElement>(null);
    const carouselRef = useRef<HTMLDivElement>(null);

    // animation on scrollf
    useEffect(() => {
        const currentRef = sectionRef.current;
        const observer = new IntersectionObserver(
            ([entry]) => {
                if (entry.isIntersecting) {
                    setIsVisible(true);
                }
            },
            {
                threshold: 0.2,
                rootMargin: "0px 0px -50px 0px",
            }
        );

        if (currentRef) {
            observer.observe(currentRef);
        }

        return () => {
            if (currentRef) {
                observer.unobserve(currentRef);
            }
        };
    }, []);

    // Auto-carousel effect
    useEffect(() => {
        if (!isVisible) return;
        const interval = setInterval(() => {
            setCurrentIndex((prevIndex) => {
                const nextIndex = prevIndex + 1;
                return nextIndex >= carousalData.length ? 0 : nextIndex;
            });
        }, 4000);
        return () => clearInterval(interval);
    }, [isVisible, carousalData.length]);

    // Smooth scroll to current index image
    useEffect(() => {
        if (carouselRef.current) {
            const scrollWidth = carouselRef.current.scrollWidth;
            const totalItems = carousalData.length;
            const itemWidth = scrollWidth / totalItems;

            carouselRef.current.scrollTo({
                left: itemWidth * currentIndex,
                behavior: "smooth",
            });
        }
    }, [currentIndex, carousalData.length]);

    return (
        <>
            <div
                ref={sectionRef}
                className=" w-full px-6 pr-0 sm:px-16 sm:pr-0 py-7 flex flex-col gap-4  sm:flex-row justify-between items-center "
            >
                {/* left  */}
                <div
                    className={`transition-all flex items-center duration-1000 ease-out pr-6 sm:p-0 sm:max-w-1/2 sm:space-x-3
             ${isVisible
                            ? "opacity-100 sm:translate-x-0"
                            : "opacity-0 sm:-translate-x-20"
                        }
          `}
                >
                    <Image
                        src={mediaLoader(animationGif)}
                        alt="everyday benefits animation"
                        className="size-22 sm:size-45"
                        width={160}
                        height={160}
                        sizes="(max-width: 640px) 88px, 136px"
                        unoptimized
                    />

                    <h6 className="text-base text-black sm:text-2xl">
                        {leftSectionTitle}
                    </h6>
                </div>

                {/* right */}
                <div
                    ref={carouselRef}
                    className={`flex rounded-3xl sm:rounded-l-3xl sm:gap-0 transition-all sm:max-w-1/2 overflow-x-auto scroll-smooth duration-1000 ease-out scrollbar-hide 
            ${isVisible
                            ? "opacity-100 translate-x-0"
                            : "opacity-0 translate-x-full sm:translate-x-20"
                        }
          `}
                >
                    {carousalData.map((benefit, index) => {
                        const isActive = index === currentIndex;
                        return (
                            <div
                                key={benefit.id}
                                className={`relative mr-5 flex-shrink-0 transition-opacity duration-700 ${isActive ? "opacity-100" : "opacity-90"
                                    }`}
                            >
                                <Image
                                    src={benefit.image}
                                    alt={`images ${benefit.image}`}
                                    width={300}
                                    height={300}
                                    className="rounded-3xl w-3xs md:max-w-none md:w-75 "
                                    sizes="(max-width: 640px) 250px, 300px"
                                />

                                <h3 className="text-base text-white absolute bottom-0 z-11 p-4">
                                    {benefit.title}
                                </h3>

                                <div
                                    className="absolute inset-0 z-10 rounded-3xl"
                                    style={{
                                        background:
                                            "linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(177, 177, 177, 0.2) 49.04%, rgba(0, 0, 0, 0.8) 87.5%, #000000 100%)",
                                    }}
                                ></div>
                            </div>
                        );
                    })}
                </div>
            </div>

            <div className="bg-primary  text-white py-6 sm:py-7 px-6 sm:px-16   mx-auto flex flex-col sm:flex-row items-center justify-between gap-8">
                <div
                    className={`flex-1 transition-all duration-1000 ease-out
          ${isVisible
                            ? "opacity-100 sm:translate-x-0"
                            : "opacity-0 sm:-translate-x-20"
                        }
          `}
                >
                    <h2 className=" font-semibold text-2xl sm:text-4xl mb-1 md:mb-2 uppercase">
                        {bannerTitle}
                    </h2>
                    <p className="text-sm sm:text-lg font-light">
                        {bannerSubtitle}
                    </p>
                </div>

                <Link
                    href={ctaLink}
                    className={`hidden sm:block bg-white text-primary px-6 py-2 rounded-md  hover:scale-101 whitespace-nowrap
            transition-all duration-1000 ease-out
          ${isVisible ? "opacity-100 translate-x-0" : "opacity-0 translate-x-20"
                        }
          `}
                >
                    {ctaText}
                </Link>
            </div>
        </>
    );
}

export default ProductBenefits;
