"use client";
import Image from "next/image";
import React, { useEffect, useState } from "react";
import { ChevronLeft, ChevronRight } from "lucide-react";
import { mediaLoader } from "@/lib/media-loader";
import Link from "next/link";

interface HeroProps {
    mobileBackgroundImage?: string;
    desktopBackgroundImage: string;
    productLogoSrc: string;
    productImageSrc: string;
    children: React.ReactNode;
    href: string;
    gradient?: string;
}

function Hero({
    mobileBackgroundImage,
    desktopBackgroundImage,
    productLogoSrc,
    productImageSrc,
    children,
    href,
    gradient
}: HeroProps) {
    const [visible, setIsVisible] = useState(false);
    // const { theme, toggleTheme } = useThemeToggler();

    useEffect(() => {
        setIsVisible(false);
        const timer = setTimeout(() => {
            setIsVisible(true);
        }, 1500);
        return () => {
            clearInterval(timer);
        };
    }, []);

    return (
        <div className="relative w-full min-h-svh">
            {/* Background Image with color Overlay */}
            <div className="absolute inset-0">
                {/* Mobile background */}
                {mobileBackgroundImage && (
                    <div
                        className="absolute inset-0 bg-cover bg-center bg-no-repeat sm:hidden"
                        style={{
                            backgroundImage: `url(${mediaLoader(mobileBackgroundImage)})`,
                        }}
                    ></div>
                )}

                {/* Desktop background (sm and above) */}
                <div
                    className="absolute inset-0 bg-cover bg-center bg-no-repeat hidden sm:block"
                    style={{
                        backgroundImage: `url(${mediaLoader(desktopBackgroundImage)})`,
                    }}
                ></div>

                {/* Solid background layer */}
                <div
                    className={`absolute inset-0 bg-primary transition-opacity duration-300 ease-in-out transform-gpu ${visible ? "opacity-0 " : "opacity-100"
                        }`}
                />

                {/* Gradient background layer */}
                <div
                    style={{
                        background:
                            gradient ?? 
                            "linear-gradient(179.98deg, var(--primary-light) 0.02%, var(--primary-dark) 87%, var(--primary) 99.98%)",
                    }}
                    className={`absolute inset-0 transition-opacity duration-2000  ease-in-out transform-gpu ${visible ? "opacity-100 " : "opacity-0 "
                        }`}
                />
            </div>

            {/* Main Content */}
            <div
                className={`relative z-10 w-full pt-25 sm:pt-[140px] sm:pb-10 max-w-7xl mx-auto px-6 md:px-16 flex items-center justify-between gap-8
        `}
            >
                <Link
                    // onClick={}
                    href={href}
                    className="text-white hover:scale-110 transition-transform  absolute left-0 "
                    aria-label="Previous slide"
                >
                    <ChevronLeft size={30} />
                </Link>

                {/* Center Content */}

                <div
                    className={`flex flex-col  sm:flex-row items-center justify-center  gap-8 md:gap-40 flex-1 transition-all duration-700 ease-out
          `}
                >
                    {/* Left Section - Text */}
                    <div
                        className={`flex flex-col items-center 
                transition-transform duration-1000 ease-out will-change-transform transform-gpu
                ${visible ? "sm:items-start translate-x-0" : "translate-x-[calc(50vw-50%-64px)]"}
            `}
                    >
                        {/* Heading */}
                        <Image
                            width={300}
                            height={260}
                            src={productLogoSrc}
                            alt="uplife gut pro logo"
                            className={`w-40 sm:w-75  mb-2 sm:mb-8 transition-all duration-200 ease-out
                            ${visible ? "" : "sm:scale-110"}`}
                            sizes="(max-width: 640px) 200px,320"
                            priority
                        />

                        <div
                            className={` text-white text-3xl transition-all duration-200 ease-out self-start
                                ${visible ? "" : "sm:scale-150"}
                        `} >
                            {children}
                        </div>
                    </div>

                    {/* Right Section - Product Image */}

                    <div
                        className={`relative w-full sm:w-auto flex sm:block justify-center transition-transform duration-1000 ease-out will-change-transform  transform-gpu
                        ${visible ? "translate-x-0" : "translate-x-full ml-auto"}
            `}
                    >
                        <Image
                            src={productImageSrc}
                            width={320}
                            height={564}
                            alt={'product packaging'}
                            className="w-75 sm:w-80 object-contain "
                            priority
                            sizes="(max-width: 640px) 300px,
                    380px"
                        />
                    </div>
                </div>

                {/* Right Arrow */}
                <Link
                    href={href}
                    // onClick={}
                    className="text-white hover:scale-110 transition-transform  right-0 absolute"
                    aria-label="Next slide"
                >
                    <ChevronRight size={30} />
                </Link>
            </div>
        </div>
    );
}

export default Hero;
