"use client";
import React, { useState, useRef, useEffect } from "react";
import Image from "next/image";
import { ChevronLeft, ChevronRight } from "lucide-react";
import { MARKETPLACES } from "./ProductsSection.constants";
import { useGlobalStore } from "@/lib/store";

export interface Thumbnail {
    id: number;
    image: string
    alt: string;
}

export interface ProductVariant {
    id: number;
    image: string;
    alt: string;
    productImages: Thumbnail[];
}

export interface ProductImageGalleryProps {
    productData: ProductVariant[];
    heading: string;
    subheading: React.ReactNode;
    description: React.ReactNode;
    smallSize?: string;
    largeSize?: string;
}

function ProductsImageGallery({
    productData,
    heading,
    subheading,
    description,
    smallSize,
    largeSize,
}: ProductImageGalleryProps) {
    const { theme } = useGlobalStore();

    const [selectedPackagingIndex, setSelectedPackagingIndex] = useState(0);
    const [selectedImage, setSelectedImage] = useState<Thumbnail>(
        productData[0].productImages[0]
    );

    const sectionRef = useRef<HTMLDivElement>(null);
    const [isVisible, setIsVisible] = useState(false);
    const [displayMarketplaces, setDisplayMarketplaces] = useState(false);

    // Get current packaging and product images
    const currentProductImages = productData[selectedPackagingIndex].productImages;
    const packagingOptions = productData;

    useEffect(() => {
        const observer = new IntersectionObserver(
            ([entry]) => {
                if (entry.isIntersecting) {
                    setIsVisible(true);
                }
            },
            { threshold: 0.1 }
        );

        const currentRef = sectionRef.current;
        if (currentRef) {
            observer.observe(currentRef);
        }

        return () => {
            if (currentRef) {
                observer.unobserve(currentRef);
            }
        };
    }, []);

    const handleClick = (thumbnail: Thumbnail) => {
        setSelectedImage(thumbnail);
    };

    const handlePrevious = () => {
        if (selectedImage.id === currentProductImages[0].id) {
            // if prevclicked on first element go to the last
            setSelectedImage(currentProductImages[currentProductImages.length - 1]);
            return;
        }
        const prevThumb = currentProductImages.find(
            (thumb: Thumbnail) => thumb.id === selectedImage.id - 1
        );
        setSelectedImage(prevThumb ? prevThumb : selectedImage);
    };

    const handleNext = () => {
        // if next cicked on last element go to the first
        if (selectedImage.id === currentProductImages[currentProductImages.length - 1].id) {
            setSelectedImage(currentProductImages[0]);
            return;
        }

        const nextThumb = currentProductImages.find(
            (thumb: Thumbnail) => thumb.id === selectedImage.id + 1
        );
        setSelectedImage(nextThumb ? nextThumb : selectedImage);
    };

    const handlePackagingChange = (index: number) => {
        setSelectedPackagingIndex(index);
        setSelectedImage(productData[index].productImages[0]);
    };

    const marketplaceUI = (
        <div className=" flex gap-2 sm:gap-3 items-center flex-wrap justify-center sm:justify-start">
            <h3 className="text-sm sm:text-base lg:text-lg text-black whitespace-nowrap w-full sm:w-auto text-center sm:text-left">
                BUY NOW:
            </h3>

            {MARKETPLACES.map((marketplace) => (
                <a
                    key={marketplace.id}
                    href={marketplace.url[theme]}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="w-14 h-14 sm:w-15 sm:h-15 px-1 py-3 sm:py-5 border-1 border-primary rounded-md flex items-center justify-center bg-primary/5 backdrop-blur-xs hover:shadow-md active:scale-95 transition-all cursor-pointer"
                >
                    {marketplace.src ? (
                        <Image
                            src={marketplace.src}
                            alt={marketplace.alt}
                            width={60}
                            height={30}
                            className="object-contain w-full h-auto border"
                        />
                    ) : (
                        <span className="text-xs text-gray-400">Logo {marketplace.id}</span>
                    )}
                </a>
            ))}
        </div>
    );

    return (
        <>
            <div className="bg-white py-7 mt-4 sm:py-16 max-w-7xl mx-auto px-6 sm:px-16">
                <div
                    ref={sectionRef}
                    className="grid grid-cols-1 lg:grid-cols-12 gap-6 lg:gap-8 items-end"
                >
                    {/* Left Side - Thumbnail Gallery */}
                    <div
                        className="hidden sm:flex lg:flex-col lg:col-span-2 order-2 lg:order-1 gap-3 sm:gap-4 overflow-x-auto lg:overflow-visible pb-2 lg:pb-0"
                        style={{
                            opacity: isVisible ? 1 : 0,
                            transform: isVisible ? "translateX(0)" : "translateX(-50px)",
                            transition: "opacity 0.8s ease-out, transform 0.8s ease-out",
                        }}
                    >
                        {currentProductImages.map((thumbnail: Thumbnail) => (
                            <div
                                onClick={() => {
                                    handleClick(thumbnail);
                                }}
                                key={thumbnail.id}
                                className="relative w-11 sm:w-18 lg:w-21 aspect-square rounded-lg cursor-pointer transition-colors overflow-hidden bg-primary/10 hover:ring-2 hover:ring-primary"
                            >
                                <Image
                                    src={thumbnail.image}
                                    alt={thumbnail.alt}
                                    fill
                                    style={{
                                        opacity: selectedImage.id === thumbnail.id ? 0.3 : 1,
                                    }}
                                    className="object-contain rounded-lg "
                                    sizes="80px"
                                />
                            </div>
                        ))}
                    </div>

                    {/* Center - Main Product Image */}
                    <div className="lg:col-span-5 order-2 lg:order-2 space-y-7">
                        <div className="relative w-full aspect-square max-w-md mx-auto">
                            <Image
                                src={selectedImage.image}
                                alt={"this image"}
                                fill
                                className="object-contain rounded-2xl sm:rounded-lg relative z-0"
                                sizes=""
                            />

                            {/* Left Navigation Button */}
                            <button
                                onClick={handlePrevious}
                                className="bg-white/30 backdrop-blur-xl rounded-lg overflow-hidden block sm:hidden absolute left-1 top-1/2 -translate-y-1/2 shadow-lg transition-all z-10"
                                aria-label="Previous image"
                            >
                                <ChevronLeft color="black" className="size-8 sm:size-6" />
                            </button>

                            {/* Right Navigation Button */}
                            <button
                                onClick={handleNext}
                                className="absolute bg-white/30 backdrop-blur-xl rounded-lg overflow-hidden block sm:hidden right-1 top-1/2 -translate-y-1/2 shadow-lg transition-all z-10"
                                aria-label="Next image"
                            >
                                <ChevronRight color="black" className="size-8 sm:size-6" />
                            </button>

                            {( selectedImage.id === 1) && largeSize && smallSize && (
                                <Image
                                    src={
                                        selectedPackagingIndex === 0
                                            ? largeSize
                                            : smallSize
                                    }
                                    alt={"5l pack"}
                                    width={140}
                                    height={140}
                                    className="w-25 sm:w-35 absolute -right-4 sm:right-3 -translate-y-2/3 sm:-translate-y-2/3 z-10"
                                    sizes="(max-width: 640px) 90px, (max-width: 1024px) 120px,140px"
                                    quality={100}
                                />
                            )}
                        </div>

                        {/* Product packagings  */}

                        <div className="flex justify-center gap-3 sm:gap-4">
                            {packagingOptions.map((packaging: ProductVariant, index: number) => (
                                <div
                                    onClick={() => handlePackagingChange(index)}
                                    key={packaging.id}
                                    className="relative w-18 lg:w-21 aspect-square rounded-lg cursor-pointer transition-colors overflow-hidden bg-primary/10 hover:ring-2 hover:ring-primary"
                                >
                                    <Image
                                        src={packaging.image}
                                        alt={packaging.alt}
                                        fill
                                        className={`object-contain rounded-lg
                                        ${selectedPackagingIndex === index
                                                ? "border-2 border-primary"
                                                : "opacity-60"
                                            }`}
                                        sizes="80px"
                                    />
                                </div>
                            ))}
                        </div>
                    </div>

                    {/* Right Side - Product Details */}
                    <div
                        className="lg:col-span-5 order-1 sm:order-3 space-y-4 sm:space-y-6 self-center"
                        style={{
                            opacity: isVisible ? 1 : 0,
                            transform: isVisible ? "translateY(0)" : "translateY(-50px)",
                            transition: "opacity 0.8s ease-out, transform 0.8s ease-out",
                        }}
                    >
                        {/* Heading */}
                        <div className="space-y-4">
                            <h1 className="text-[32px] tracking-wider text-center sm:text-left sm:text-4xl lg:text-5xl  text-black">
                                {heading}
                            </h1>

                            <h2 className="italic uppercase text-xl sm:text-2xl lg:text-3xl/7 font-semibold text-white bg-primary py-1.5 sm:py-6  text-center rounded-full">
                                {subheading}
                            </h2>
                        </div>

                        {/* Description   */}
                        <div
                            className="text-center text-black font-light text-sm sm:text-lg"
                            style={{
                                opacity: isVisible ? 1 : 0,
                                transform: isVisible ? "translateY(0)" : "translateY(50px)",
                                transition:
                                    "opacity 0.8s ease-out 0.2s, transform 0.8s ease-out 0.2s",
                            }}
                        >
                            {description}
                        </div>

                        <div className="hidden sm:flex">{marketplaceUI}</div>
                    </div>
                </div>

                <button
                    onClick={() => setDisplayMarketplaces(!displayMarketplaces)}
                    className="sm:hidden bg-primary rounded-md py-2.5 w-full text-base text-center text-white mt-6"
                >
                    Find in Store
                </button>

                <div
                    className="transition-all duration-2000 ease-out mt-1 sm:hidden"
                    style={{
                        opacity: displayMarketplaces ? 1 : 0,
                    }}
                >
                    {displayMarketplaces && marketplaceUI}
                </div>
            </div>
        </>
    );
}

export default ProductsImageGallery;
