"use client";
import Image from "next/image";
import React, { useEffect, useRef, useState } from "react";
import { Search } from "lucide-react";

interface CommonSemiCircleProps {
  backgroundImage: string;
  heading: string;
  searchPlaceholder: string;
  descriptionTop: string;
  descriptionBottom: string;
  containerClassName?: string;
}

function CommonSemiCircle({
  backgroundImage,
  heading,
  searchPlaceholder,
  descriptionTop,
  descriptionBottom,
  containerClassName = "aspect-2",
}: CommonSemiCircleProps) {
  const [isVisible, setIsVisible] = useState(false);
  const sectionRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const currentRef = sectionRef.current;
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
      },
      {
        threshold: 0.1, // Trigger when 10% of the component is visible
        rootMargin: "0px 0px -100px 0px", // Trigger slightly before it comes into view
      }
    );

    if (currentRef) {
      observer.observe(currentRef);
    }

    return () => {
      if (currentRef) {
        observer.unobserve(currentRef);
      }
    };
  }, []);

  return (
    <div ref={sectionRef} className={`w-full bg-primary ${containerClassName}`}>
      <div
        className={`relative h-full w-full rounded-t-full bg-primary mx-auto 
            transition-all duration-1000 ease-in-out 
        ${isVisible ? "opacity-100" : "opacity-0 "}
        `}
      >
        <Image
          src={backgroundImage}
          alt="Background"
          width={1280}
          height={640}
          sizes="100vw"
          className={`w-full h-full rounded-t-full z-1 object-cover object-center pointer-events-none `}
        />

        {/* Hero Content Overlay */}
        <div className="absolute inset-0 z-10 flex flex-col items-center justify-end mb-1 sm:justify-evenly py-8 px-6 sm:px-10 md:px-12 lg:px-16 sm:py-0 text-white">
          <div className="sm:max-w-xs md:max-w-md lg:max-w-xl w-full text-center hidden sm:block">
            {/* Main Heading */}

            <div className="bg-white/25 border-2 border-white backdrop-blur-xl rounded-full px-6 py-3 md:px-5 md:py-2.5 lg:px-6 lg:py-3 inline-flex items-center gap-3 mb-2 sm:mb-3">
              <Search className="w-5 h-5 sm:w-5 sm:h-5 md:w-5 md:h-5 lg:w-6 lg:h-6 text-white flex-shrink-0" />

              <div className="text-white text-sm sm:text-xs md:text-base lg:text-lg">
                {searchPlaceholder}
              </div>
            </div>


            <h1 className="text-xl sm:text-xl md:text-2xl lg:text-3xl mb-1 sm:mb-2 md:mb-2.5 lg:mb-3 font-medium text-shadow-[2px_2px_8px_#000]/75">
              {heading}
            </h1>


            <p className="text-base sm:text-base md:text-base lg:text-lg font-light leading-relaxed ">
              {descriptionTop}
            </p>

          </div>

          {/* Description Text */}
          <div className="max-w-[300px] sm:max-w-md md:max-w-lg lg:max-w-2xl text-center justify-self-start">
            <p className="font-light text-xs sm:text-lg md:text-xl lg:text-2xl">
              {descriptionBottom}
            </p>
          </div>
        </div>

        <Image
          src="/products-page/curve-white.svg"
          alt="curveWhite"
          width={1280}
          height={150}
          sizes="100vw"
          className="absolute inset-x-0 w-full bottom-0 z-3 pointer-events-none"
        />

        <div className="bg-black/80 absolute inset-0 rounded-t-full z-2 pointer-events-none"></div>
      </div>
    </div>
  );
}

export default CommonSemiCircle;
